import React, { Fragment } from "react";
import { MdWarning } from "react-icons/md";
import { RiErrorWarningFill } from "react-icons/ri";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  TeacherData,
  TeacherDataRole,
  FC_GetTeachersBySchool,
  FC_DisableUser,
  FT_DisableUser,
  // FC_GetSchoolDetailsRegister,
  API_DataGetSchoolRegister,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import { UpdateStuffDetails } from "../../components/UpdateStuffDetails/UpdateStuffDetails";
import { UserProfile } from "../../containers/UserProfile/UserProfile";
import { StoreState } from "../../reducers";
import { checkAccess, UsersAccess } from "../../utils/access";
import { isEmptyOrSpaces, search } from "../../utils/functions";
import validateStaff from "../../utils/validatedStaff";
import Button from "../Buttons/Button";
// import { RenderPageAccess, UsersAccess } from "../../utils/access";
// import { AssignStuffComponent } from "../AssignStuffComponent/AssignStuffComponent";
// import Button from "../Buttons/Button";
import AddUserPosition from "./AddUserPosition";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  FC_DisableUser: FT_DisableUser;
  school_code?: string;
}

interface AppState {
  loading: boolean;
  error: {
    target:
      | "requests-error"
      | "loading-update-error"
      | "main"
      | "main-success"
      | "update-error"
      | "main-error"
      | null;
    msg: string;
  };
  data: TeacherData[];
  roles: TeacherDataRole[];
  loading_data: boolean;
  selected_role: string;
  selected_request_id: string;
  selected_value: string;
  selected_comment: string;
  selected_auto_level: string;
  search: string;
  selected_qualification: string;
  edit_staff_id: string;
  disable_user_loading: boolean;
  addUserPositions: TeacherData | null;
  view_staff_id: string;
  change_teacher_post: TeacherData | null;
  // Register
  school_register_assets: API_DataGetSchoolRegister | null;
  school_register_assets_loading: boolean;
  selected_position_id: string;
  selected_qualification_id: string;
  selected_school_code: string;

  selected_check_validation: string;
}

class _StaffListComponent extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      disable_user_loading: false,
      loading: false,
      data: [],
      roles: [],
      error: {
        target: null,
        msg: "",
      },
      loading_data: true,
      selected_role: "",
      selected_request_id: "",
      selected_value: "0",
      selected_comment: "",
      selected_auto_level: "",
      search: "",
      selected_qualification: "",
      edit_staff_id: "",
      addUserPositions: null,
      view_staff_id: "",
      change_teacher_post: null,
      // Register
      school_register_assets: null,
      school_register_assets_loading: true,
      selected_position_id: "",
      selected_qualification_id: "",
      selected_school_code: "",
      selected_check_validation: "",
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();
  };

  componentDidMount = () => {
    this.startApp();
  };

  DisableUser = (user_id: string) => {
    console.log("deleting candidate");

    if (
      window.confirm("Are you sure you want to deactivate this user?") === true
    ) {
      this.setState({
        disable_user_loading: true,
      });
      this.props.FC_DisableUser(user_id, (status, msg) => {
        if (status === true) {
          this.setState({
            data: this.state.data.filter((itm) => itm.user_id !== user_id),
            disable_user_loading: false,
          });
        } else {
          this.setState({
            disable_user_loading: false,
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      });
    }
  };

  startApp = () => {
    // console.log("loading candidate od the school");

    if (this.props.basic.academic_calender === null) {
      this.setState({
        error: {
          target: "main-error",
          msg: "failed to load data, please reload the page (Refresh)",
        },
      });
    } else if (
      this.props.auth.school === null &&
      this.props.school_code === undefined
    ) {
      this.setState({
        error: {
          target: "main-error",
          msg: "No school assigned to you",
        },
      });
    } else {
      let school_code =
        this.props.school_code === undefined
          ? this.props.auth.school?.school_code
          : this.props.school_code;

      // selectSchool = (el: SchoolInterface | null) => {
      //   if (el !== null && this.props.basic.academic_calender !== null) {
      //     this.setState({ school_loading: true });

      // }
      //   this.setState({ selected_school: el });
      // };

      this.setState({
        error: {
          target: null,
          msg: "",
        },
        loading_data: true,
      });

      if (school_code === undefined)
        return this.setState({
          error: {
            target: "main-error",
            msg: "Invalid school ID",
          },
        });

      FC_GetTeachersBySchool(school_code, (status, data, msg) => {
        if (status === true && data !== null) {
          // console.log({ status, data, msg });
          this.setState({
            loading_data: false,
            data: data.staff.filter((itm) => itm.status === "Active"),
            roles: data.role,
          });
        } else {
          this.setState({
            loading_data: false,
            error: {
              msg: msg,
              target: "main-error",
            },
          });
        }
      });
    }
  };

  /**
   * Select assets for teacher
   * @returns
   */
  render() {
    if (this.state.error.target === "main-error") {
      return (
        <Alert theme="danger" fixed>
          {this.state.error.msg}
        </Alert>
      );
    }
    let RequestsList: TeacherData[] =
      this.state.selected_role === ""
        ? this.state.data
        : this.state.data.filter(
            (itm) => itm.role_id === this.state.selected_role
          );

    // if (
    //   this.state.change_teacher_post !== null &&
    //   this.props.school_code !== undefined
    // )
    //   return (
    //     <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
    //       <div className="flex p-2 border-b">
    //         <h2 className="text-2xl flex-1">Change user post</h2>
    //         <Button
    //           onClick={() => this.setState({ change_teacher_post: null })}
    //           theme="danger-light"
    //         >
    //           Close
    //         </Button>
    //       </div>
    //       <div className="">

    //       </div>
    //     </DashboardContainer>
    //   );

    // let non_validated = RequestsList.filter(
    //   (itm) => !(itm.first_name == null || itm.first_name === "")
    // ).length;

    RequestsList =
      this.state.selected_qualification === ""
        ? RequestsList
        : RequestsList.filter(
            (itm) => itm.qualification === this.state.selected_qualification
          );

    RequestsList = search(RequestsList, this.state.search, {
      email: true,
      first_name: true,
      full_name: true,
      last_name: true,
      phone_numbers: true,
      qualification: true,
      role: true,
      role_id: true,
      status: true,
      user_id: true,
      username: true,
      nid: true,
    });

    if (this.state.loading_data === true)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (this.state.edit_staff_id !== "")
      return (
        <UpdateStuffDetails
          user_id={this.state.edit_staff_id}
          success={(user_id, data) => {
            this.setState({
              data: this.state.data.map((itm) =>
                itm.user_id === user_id
                  ? {
                      ...itm,
                      email: data.email,
                      first_name: data.first_name,
                      full_name: data.full_name,
                      last_name: data.last_name,
                      phone_numbers: data.phone_numbers,
                      username: data.username,
                    }
                  : itm
              ),
            });
          }}
          formAccess={{
            first_name: true,
            middle_name: true,
            last_name: true,
            full_name: true,
            phone_numbers: true,
            email: true,
            username: true,
            staff_code: true,
            sex: true,
            martial_status: true,
            highest_qualification_id: true,
            dob: true,
            rssb_number: true,
            nationality_id: true,
            bank_account: true,
            bank_id: true,
            specialization_id: true,
            village_code: false,
            contract_type: true,
            education_domain_id: true,
            education_sub_domain_id: true,
            graduation_date: true,
            hired_date: false,
            staff_category_id: true,
            education_certificate_id: true,
            teaching_level_id: true,
          }}
          required={{
            first_name: true,
            middle_name: false,
            last_name: true,
            full_name: true,
            phone_numbers: true,
            email: false,
            username: true,
            staff_code: true,
            sex: true,
            martial_status: true,
            highest_qualification_id: true,
            dob: true,
            rssb_number: true,
            nationality_id: true,
            bank_account: true,
            bank_id: true,
            specialization_id: true,
            village_code: false,
            contract_type: true,
            education_domain_id: true,
            education_sub_domain_id: true,
            graduation_date: true,
            hired_date: false,
            staff_category_id: true,
            education_certificate_id: true,
            teaching_level_id: true,
          }}
          close={() => this.setState({ edit_staff_id: "" })}
        />
      );

    if (
      !isEmptyOrSpaces(this.state.view_staff_id) &&
      this.state.addUserPositions === null &&
      isEmptyOrSpaces(this.state.edit_staff_id)
    )
      return (
        <UserProfile
          isExternal={true}
          user_id={this.state.view_staff_id}
          close={() => this.setState({ view_staff_id: "" })}
          setEditStaff={(value: string) =>
            this.setState({ edit_staff_id: value })
          }
          user_data={RequestsList.find(
            (item) => item.user_id === this.state.view_staff_id
          )}
          setAddUserPositions={(value: TeacherData) =>
            this.setState({ addUserPositions: value })
          }
          DisableUser={(value: string) => this.DisableUser(value)}
        />
      );

    return (
      <Fragment>
        <>
          {this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error.msg}
            </Alert>
          )}

          {this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error.msg}
            </Alert>
          )}

          {this.state.disable_user_loading === true && (
            <Alert theme="info" fixed>
              <span className="animate__animated animate__fadeIn animate__infinite">
                Disabling...
              </span>
            </Alert>
          )}

          <DashboardContainer>
            {this.state.error.target === "main" && (
              <Alert theme="danger">{this.state.error.msg}</Alert>
            )}
            {this.state.error.target === "loading-update-error" && (
              <Alert theme="danger">{this.state.error.msg}</Alert>
            )}

            <div className="flex items-center">
              <div className="flex-1">
                <Heading>Staff list</Heading>
                <p className="opacity-60 text-lg -mt-1 font-bold text-accent-900 dark:text-accent-800">
                  {this.props.auth.school?.school_name}
                </p>
              </div>
              <div className="">
                {/* <Link to="/register-administration-staff">
                  <Button size="lg">Add new Staff</Button>
                </Link> */}
              </div>
            </div>

            <div className="mt-3">
              <div className="border border-t mt-2 mb-1 flex items-center pl-3 pr-2 rounded">
                <div className="flex-1 pr-2">
                  <TextInput
                    error=""
                    label="Search"
                    name=""
                    type="search"
                    onChange={(e) => this.setState({ search: e.target.value })}
                    placeholder="Search by name, nid"
                    value={this.state.search}
                    size="sm"
                  />
                </div>
                {!this.props.auth.school && (
                  <div className="">
                    <InputSelect
                      data={[
                        { title: "All Occupations", value: "" },
                        ...this.state.roles.map((el) => ({
                          title: el.role,
                          value: el.role_id,
                        })),
                      ]}
                      error=""
                      name="select_role"
                      onChange={(e) =>
                        this.setState({ selected_role: e.target.value })
                      }
                      label="Occupation"
                      placeholder="Select Occupation"
                      value={this.state.selected_role}
                      size="sm"
                    />
                  </div>
                )}
                {this.props.basic.qualifications !== null && (
                  <div className="px-2">
                    <InputSelect
                      data={[
                        { title: "All", value: "" },
                        ...this.props.basic.qualifications.map((el) => ({
                          title: el.qualification_name,
                          value: el.qualification_name,
                        })),
                      ]}
                      error=""
                      name="select_qualification"
                      onChange={(e) =>
                        this.setState({
                          selected_qualification: e.target.value,
                        })
                      }
                      label="Qualification"
                      placeholder="Select Qualification"
                      value={this.state.selected_qualification}
                      size="sm"
                    />
                  </div>
                )}
                <div className="p-2 px-3 ml-3 bg-accent-800 bg-opacity-30 rounded">
                  <label className="">Results:</label>
                  <h4 className="text-2xl -mt-1 font-bold">
                    {RequestsList.length}
                  </h4>
                </div>
              </div>
              {/* <div className="pb-2">
                Non validated: <b>{non_validated}</b>
              </div> */}

              <table className="w-full text-left table-auto">
                <thead>
                  <tr>
                    <th className="p-2 border">#</th>
                    <th className="p-2 border">NID/Passport</th>
                    <th className="p-2 border">Staff code</th>
                    <th className="p-2 border">Names</th>
                    <th className="p-2 border">Phone number</th>
                    <th className="p-2 border">Position</th>
                    <th className="p-2 border">Occupation</th>
                    <th className="p-2 border">Validated</th>
                    <th className="p-2 border"></th>

                    {this.props.auth.role !== null &&
                      this.props.auth.role.role_id !== "2" &&
                      this.props.auth.role.role_id !== "1" && (
                        <>
                          <th className="p-2 border">Qualification</th>
                          <th className="p-2 border">School</th>
                        </>
                      )}
                    {this.props.school_code === undefined && <th></th>}
                  </tr>
                </thead>
                <tbody>
                  {RequestsList.length <= 0 ? (
                    <tr>
                      <td
                        colSpan={8}
                        className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                      >
                        <Heading size="md" className="opacity-60 font-light">
                          No results found
                        </Heading>
                      </td>
                    </tr>
                  ) : (
                    RequestsList.map((req, i) => {
                      let validatedCheck = validateStaff(req);
                      return (
                        <Fragment key={i}>
                          <tr className="group border rounded hover:bg-yellow-600 hover:bg-opacity-10 text-sm">
                            <td className="p-2 border" key={i}>
                              {i + 1}
                            </td>
                            <td className="p-2 border">{req.nid}</td>
                            <td className="p-2 border">{req.staff_code}</td>
                            <td className="p-2 border">{req.full_name}</td>
                            <td className="p-2 border">{req.phone_numbers}</td>
                            <td className="p-2 border">{req.position}</td>
                            <td className="p-2 border">{req.role}</td>
                            <td className="w-24 text-center parent-hide-child">
                              {validatedCheck.validated === false ? (
                                <button
                                  onClick={() =>
                                    this.setState({
                                      selected_check_validation: req.user_id,
                                    })
                                  }
                                  className="text-xs bg-red-400 text-white p-0.5 rounded-full"
                                >
                                  Non-Validated
                                </button>
                              ) : (
                                <span className="text-xs bg-green-500 text-white p-0.5 rounded-full">
                                  Validated
                                </span>
                              )}
                            </td>
                            {this.props.auth.role !== null &&
                              this.props.auth.role.role_id !== "2" &&
                              this.props.auth.role.role_id !== "1" && (
                                <>
                                  <td className="p-2 border">
                                    {req.qualification}
                                  </td>
                                  <td className="p-2 border">
                                    {req.school?.school_name}
                                  </td>
                                </>
                              )}
                            {this.props.school_code === undefined && (
                              <td className="p-1 border">
                                <button
                                  onClick={() =>
                                    this.setState({
                                      view_staff_id: req.user_id,
                                    })
                                  }
                                  className="opacity-80 group-hover:opacity-100 bg-accent-800 bg-opacity-0 hover:bg-opacity-100 hover:text-white text-accent-900 font-semibold py-2 px-4 rounded"
                                >
                                  View
                                </button>
                              </td>
                            )}
                          </tr>
                          {this.state.selected_check_validation ===
                            req.user_id && (
                            <tr>
                              <td colSpan={10} className="pb-5">
                                <div className="pb-2 rounded-b-lg -mt-1 animate__animated animate__zoomIn animate__faster border bg-red-100">
                                  <div className="p-1 px-2 flex bg-red-200">
                                    <h2 className="flex-1 text-lg font-bold">
                                      Please validate
                                    </h2>
                                    <Button
                                      className=""
                                      size="sm"
                                      theme="danger-light"
                                      onClick={() =>
                                        this.setState({
                                          selected_check_validation: "",
                                        })
                                      }
                                    >
                                      Close
                                    </Button>
                                  </div>

                                  <div className="px-2 gap-1 rounded grid md:grid-cols-2 lg:grid-cols-3 pt-2">
                                    {validatedCheck.errors.map((itm, i) => (
                                      <div
                                        key={i}
                                        className="border rounded full mb-1 mx-2 border-red-300 bg-white p-1 flex"
                                      >
                                        <MdWarning className="pr-1 text-xl inline-block text-yellow-600" />{" "}
                                        <p>{itm}</p>
                                      </div>
                                    ))}
                                  </div>
                                </div>
                              </td>
                            </tr>
                          )}
                        </Fragment>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          </DashboardContainer>
        </>
        {checkAccess(
          this.props.auth?.role?.access,
          UsersAccess.EDIT_USERS_POSITION
        ) &&
          this.state.addUserPositions !== null &&
          this.props.auth.school !== null && (
            <Modal
              backDrop={true}
              theme={Themes.default}
              close={() => {
                this.setState({ addUserPositions: null });
              }}
              backDropClose={
                this.state.addUserPositions.qualification === null ||
                this.state.addUserPositions.qualification === ""
                  ? false
                  : true
              }
              widthSizeClass={ModalSize.large}
              displayClose={
                this.state.addUserPositions.qualification === null ||
                this.state.addUserPositions.qualification === ""
                  ? false
                  : true
              }
              title={
                this.state.addUserPositions.qualification === null ||
                this.state.addUserPositions.qualification === "" ? (
                  ""
                ) : (
                  <div className="text-2xl w-full text-primary-800 -mb-2">
                    ADD USER POSITION
                  </div>
                )
              }
              padding={{
                title: true,
              }}
            >
              {this.state.addUserPositions.qualification === null ||
              this.state.addUserPositions.qualification === "" ? (
                <div className="p-7">
                  <div className="flex flex-row items-center gap-4">
                    <div>
                      <RiErrorWarningFill className="text-8xl text-gray-300" />
                    </div>
                    <div>
                      <div className="text-accent-900 font-extrabold text- text-3xl">
                        Staff qualification is missing
                      </div>
                      <div className="text-black text- text-lg">
                        Please assign qualification. Go to staff profile, then
                        click on change school/position to add qualification
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center justify-center mt-4">
                    <div
                      onClick={() => this.setState({ addUserPositions: null })}
                      className="bg-primary-800 text-white rounded text-base font-bold text-center px-3 py-2 cursor-pointer w-max"
                    >
                      Click to add qualification
                    </div>
                  </div>
                </div>
              ) : (
                <AddUserPosition
                  user={this.state.addUserPositions}
                  school={this.props.auth.school}
                  auth={this.props.auth}
                  loading={this.state.loading}
                  basic={this.props.basic}
                  close={() => this.setState({ addUserPositions: null })}
                  updateUserPosition={(
                    user_id_test: string,
                    position_name: string
                  ) => {
                    let updatedData = [];
                    for (let selected of this.state.data) {
                      if (selected.user_id === user_id_test) {
                        updatedData.push({
                          email: selected.email,
                          first_name: selected.first_name,
                          full_name: selected.full_name,
                          last_name: selected.last_name,
                          phone_numbers: selected.phone_numbers,
                          qualification: selected.qualification,
                          role: selected.role,
                          role_id: selected.role_id,
                          status: selected.status,
                          user_id: selected.user_id,
                          position: position_name,
                          username: selected.username,
                          bank_account: selected.bank_account,
                          dob: selected.dob,
                          marital_status: selected.marital_status,
                          nationality_id: selected.nationality_id,
                          nid: selected.nid,
                          rssb_number: selected.rssb_number,
                          location: selected.location,
                          school: selected.school,
                          sex: selected.sex,
                          specialization: selected.specialization,
                          staff_category: selected.staff_category,
                          staff_code: selected.staff_code,
                          start_date_in_the_school:
                            selected.start_date_in_the_school,
                          teacher_hierarch: selected.teacher_hierarch,
                        });
                      } else {
                        updatedData.push(selected);
                      }
                    }
                    this.setState({ data: updatedData });
                    // this.setState({ addUserPositions: null });
                  }}
                />
              )}
            </Modal>
          )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const StaffListComponent = connect(mapStateToProps, { FC_DisableUser })(
  _StaffListComponent
);
