import axios from "axios";
import React, { useEffect, useState } from "react";
import { RiErrorWarningFill } from "react-icons/ri";
import { Link } from "react-router-dom";
import {
  Auth,
  AuthSchool,
  Basic,
  FC_GetSchoolPositions,
  TeacherData,
} from "../../actions";
import { APP_URL } from "../../config/app.config";
import {
  PositionsOnSchool,
  SchoolPositionsCombination,
  // vacantPosts,
} from "../../containers/Head-Teacher/Request/HeadTeacherRequestToDee";
import Loading from "../Loading/Loading";
// import {
//   PositionsOnSchool,
//   SchoolPositionsCombination,
// } from "../Head-Teacher/Request/HeadTeacherRequestToDee";

export const filteredUserPositionData = (
  schoolPosition: PositionsOnSchool[] | null,
  user: TeacherData
) => {
  // if (schoolPositionTest !== null) {
  if (schoolPosition !== null) {
    // return schoolPositionTest.filter(
    return schoolPosition.filter(
      (itm) =>
        itm.qualification_name === user.qualification && schoolPosition !== null
      // && vacantPosts(schoolPosition, schoolPositionTest, itm) !== -1
    );
  } else {
    return [];
  }
};

interface AddUserPositionProps {
  user: TeacherData;
  basic: Basic;
  school: AuthSchool;
  auth: Auth;
  loading: boolean;
  close?: () => void;
  updateUserPosition: (user_id: string, position_name: string) => void;
}

const AddUserPosition = (props: AddUserPositionProps) => {
  const [schoolPosition, setSchoolPosition] = useState<
    PositionsOnSchool[] | null
  >(null);
  // const [schoolPositionTest, setSchoolPositionTest] = useState<
  //   PositionsOnSchool[] | null
  // >(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [selectedPosition, setSelectedPosition] = useState<string>("");
  const [submittedPosition, setSubmittedPosition] = useState<string | null>(
    null
  );

  // const GetDistributedNumber = (position_code: string): number => {
  //   if (schoolPositionTest) {
  //     let test = schoolPositionTest.find(
  //       (itm) => itm.position_code === position_code
  //     );
  //     if (test !== undefined) {
  //       if (
  //         test.dde_post_distribution !== undefined &&
  //         test.dde_post_distribution !== null
  //       ) {
  //         return parseInt(test.dde_post_distribution);
  //       }
  //     }
  //   }
  //   return 0;
  // };

  const AddUserToPosition = async () => {
    if (props.auth.user && selectedPosition !== "") {
      setLoading(true);
      try {
        const res = await axios.post(`${APP_URL}/user/assign/position`, {
          user_id: props.user.user_id,
          school_code: props.school.school_code,
          position_code: selectedPosition,
        });
        if (schoolPosition !== null) {
          let test = schoolPosition.find(
            (itm) => itm.position_code === selectedPosition
          );
          if (test !== undefined) {
            setSubmittedPosition(test.position_name);
            props.updateUserPosition(props.user.user_id, test.position_name);
          }
        }
        console.log("Success: ", res);
        props.close && props.close();
        alert("User position assignment done successfully!");
      } catch (error: any) {
        console.log("Err: ", error);
      }
      setLoading(false);
    } else {
      alert("Please select a position!");
    }
  };

  useEffect(() => {
    if (schoolPosition === null) {
      props.basic.academic_calender &&
        FC_GetSchoolPositions(
          props.school.school_code,
          props.basic.academic_calender.academic_year_id,
          (
            status: boolean,
            data: SchoolPositionsCombination | null,
            msg: string
          ) => {
            setLoading(true);
            if (status === true && data !== null) {
              setSchoolPosition(data.school_positions); //normals
              // setSchoolPositionTest(data.school_post_requests); //requests
              setLoading(false);
            }
          }
        );
    }
  });
  if (filteredUserPositionData(schoolPosition, props.user).length === 0) {
    return (
      <div className="p-7 mb-7">
        <div className="flex flex-row items-center gap-4">
          <div>
            <RiErrorWarningFill className="text-8xl text-gray-300" />
          </div>
          <div>
            <div className="text-accent-900 font-extrabold text- text-3xl flex flex-row items-center gap-3">
              <span>Missing positions for </span>
              <div className="font-extrabold bg-accent-900 text-white px-1 rounded-md w-max text-2xl">
                {props.user.qualification}
              </div>
            </div>
            <div className="text-gray-600 text- text-lg">
              There are no available positions for selected qualification in the
              school. Please, you need to add positions first before assigning
              to a staff!
            </div>
            <div className="flex items-center justify-cente mt-4">
              <Link to="/assign-school-position">
                <div className="bg-primary-800 text-white rounded text-base font-bold text-center px-3 py-2 cursor-pointer w-max">
                  Click to add school positions
                </div>
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }
  return (
    <div className="px-4 pb-4 bg-gray-50 border-t rounded-b">
      <div>
        <div>School name: </div>
        <div className="text-xl font-bold">
          {props.school.school_name} ({props.school.school_code})
        </div>
      </div>
      <div>
        <div>Teacher: </div>
        <div className="text-xl font-bold text-primary-800">
          {props.user.first_name} {props.user.last_name} ({props.user.role})
        </div>
      </div>
      {loading === true ? (
        <div className="flex flex-col items-center justify-center w-full p-3 rounded">
          <Loading />
        </div>
      ) : submittedPosition !== null ? (
        <div>
          <div>Submitted position: </div>
          <div className="text-xl font-bold text-green-700 bg-green-100 rounded-lg px-4 py-2">
            {submittedPosition}
          </div>
        </div>
      ) : (
        props.user.position !== null && (
          <div>
            <div>Current position: </div>
            <div className="text-xl font-bold text-green-700 bg-green-100 rounded-lg px-4 py-2">
              {props.user.position}
            </div>
          </div>
        )
      )}
      {loading === true || schoolPosition === null || props.loading === true ? (
        <div className="text-2xl font-bold text-yellow-700 text-center">
          Loading, Please wait...
        </div>
      ) : (
        <div>
          <div>
            {props.user.position === null
              ? `Choose position for (${props.user.qualification})`
              : `Update position for (${props.user.qualification})`}
            :{" "}
          </div>
          <div>
            {filteredUserPositionData(schoolPosition, props.user).length ===
            0 ? (
              <div className="bg-red-100 text-red-900 font-bold px-4 py-2 text-xl rounded">
                No available posts on your qualification (
                {props.user.qualification})
              </div>
            ) : (
              <select
                value={selectedPosition}
                onChange={(e) => setSelectedPosition(e.target.value)}
                className="px-3 py-2 rounded-md w-full border-2 border-primary-900"
              >
                <option value="">Choose school position</option>
                {filteredUserPositionData(schoolPosition, props.user)
                  .filter((itm) => itm.position_name !== props.user.position)
                  .map((item, i) => (
                    <option
                      // disabled={
                      //   (schoolPositionTest === null
                      //     ? 0
                      //     : vacantPosts(
                      //         schoolPosition,
                      //         schoolPositionTest!,
                      //         item
                      //       )) > 0
                      //     ? false
                      //     : true
                      // }
                      value={item.position_code}
                      key={i + 1}
                    >
                      {
                        item.position_name +
                          " (" +
                          item.qualification_name +
                          ")"
                        // in " + item.school_level_name
                        // + " (" +
                        // (schoolPositionTest === null
                        //   ? 0
                        //   : vacantPosts(
                        //       schoolPosition,
                        //       schoolPositionTest!,
                        //       item
                        //     )) +
                        // ")"
                      }
                    </option>
                  ))}
              </select>
            )}
          </div>
        </div>
      )}
      {loading === false && (
        <div className="flex flex-row justify-between mt-2">
          <div
            onClick={() => props.close && props.close()}
            className="bg-gray-200 text-black px-4 py-2 rounded cursor-pointer w-max"
          >
            Cancel
          </div>
          <div
            onClick={() => {
              AddUserToPosition();
            }}
            className="bg-primary-800 text-white px-4 py-2 rounded cursor-pointer w-max"
          >
            Submit
          </div>
        </div>
      )}
    </div>
  );
};

export default AddUserPosition;
