import React, { FC, ReactElement, useEffect, useState } from "react";
import { FC_GetRoles, FC_UpdateRole, Role__Interface } from "../../actions";
import Alert from "../Alert/Alert";
import Button from "../Buttons/Button";
import InputSelect from "../Inputs/InputSelect";
import Loading from "../Loading/Loading";
import Modal, { Themes, ModalSize } from "../Modal/Modal";

type UpdateRoleProps = {
  close: () => void;
  role_id: string | undefined | null;
  success: (role_id: string) => void;
  user_id: string;
};

const UpdateRole: FC<UpdateRoleProps> = ({
  close,
  role_id,
  success,
  user_id,
}): ReactElement => {
  console.log({ role_id, user_id });

  const [role, setRole] = useState("");
  const [loading, setLoading] = useState(true);
  const [loading_update, setLoadingUpdate] = useState(false);
  const [error, setError] = useState("");
  const [success_msg, setSuccessMsg] = useState("");
  const [role_list, setRoleList] = useState<Role__Interface[]>([]);

  useEffect(() => {
    if (role === "" && role_id && loading === true) setRole(role_id);
    console.log({ role_id });

    if (loading === true)
      FC_GetRoles((status, data, msg) => {
        setLoading(false);
        if (!status) setError(msg);
        setRoleList(data ? data : []);
      });
  }, [loading, role, role_id]);

  const UpdateRole = (e: any) => {
    e.preventDefault();
    if (role === "") return setError("Please select a role");
    setLoadingUpdate(true);

    FC_UpdateRole(
      {
        role_id: role,
        user_id: user_id,
      },
      (status, msg) => {
        setLoadingUpdate(false);
        if (status === true) {
          setSuccessMsg("User Role has been updated");
          success(role);
        }
        if (status === false) setError(msg);
      }
    );
  };

  /* function body */
  return (
    <Modal
      backDrop={true}
      theme={Themes.default}
      close={close}
      backDropClose={false}
      widthSizeClass={ModalSize.medium}
      displayClose={true}
      title="Change role"
      padding={{
        title: true,
        body: undefined,
        footer: undefined,
      }}
    >
      <div className="border-t p-2">
        {error !== "" && (
          <Alert theme="danger" onClick={() => setError("")}>
            {error}
          </Alert>
        )}
        {success_msg !== "" && (
          <Alert theme="success" onClick={() => setSuccessMsg("")}>
            {success_msg}
          </Alert>
        )}

        {loading === true ? (
          <div className="text-center p-5">
            <Loading />
          </div>
        ) : (
          <div className="p-1">
            <form onSubmit={UpdateRole}>
              <InputSelect
                onChange={(event: React.ChangeEvent<HTMLSelectElement>) => {
                  setRole(event.target.value);
                  setError("");
                }}
                value={role ? role : ""}
                error={""}
                label={"Select the role"}
                placeholder={""}
                name={""}
                data={[
                  {
                    title: "Select a role",
                    value: "",
                  },
                  ...role_list.map((el) => ({
                    title: el.role,
                    value: el.role_id,
                  })),
                ]}
                disabled={loading_update}
              />
              <div className="pb-1.5 text-right">
                <Button type="submit" loading={loading_update}>
                  Update role
                </Button>
              </div>
            </form>
          </div>
        )}
      </div>
    </Modal>
  );
};
export default UpdateRole;
